:root {
  --list-padding: 10px;
  --list-margin: 4px;
  --sidebar-width: 350px;
  --line-thickness-thin: 2px;
  --primary-color: #4caf50;
  --primary-color-hover: #449e47;
  --secondary-color: #f44336;
  --secondary-color-hover: #da3c30;
  --outline-color: #3498db;
  --outline-color-hover: #2c85c0;
}

* {
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;
  font-family: Arial, sans-serif;
  margin: 0;
  height: 100vh;
  background-color: #e0e0e0;
  color: #333;
  max-width: 1600px;
  margin: 0 auto;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

/* Header Styles */
header {
  background-color: var(--primary-color);
  color: white;
  padding: 10px;
  display: flex;
  align-items: center;
}
header svg {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}

/* Main Layout Styles */
main {
  display: flex;
  flex: 1;
}

/* Sidebar Styles */
#sidebar {
  width: var(--sidebar-width);
  background-color: #f4f4f4;
  padding: 20px 10px;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.05);
  z-index: 1;
  overflow-y: auto;
  max-height: 100vh; /* Set max-height to fill remaining viewport height minus padding */
}
#sidebar-head {
  display: flex;
}
#sidebar-head h2 {
  margin-top: 0;
}
#sidebar-head #reset-all-btn {
  margin: 0;
  padding: 0;
  width: 36px;
  height: 36px;
  min-width: 36px;
  margin-left: auto;
  background-color: var(--secondary-color);
}
#sidebar-head #reset-all-btn:hover {
  background-color: var(--secondary-color-hover);
}
#sidebar-head #reset-all-btn svg {
  width: 100%;
  height: 100%;
}
#project-list {
  padding: 0;
  list-style: none;
}

/* Project and Task List Styles */
.project,
.task {
  display: flex;
  align-items: center;
  padding: var(--list-padding);
  margin: var(--list-margin) 0;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 5px;
  cursor: pointer;
  user-select: none;
  min-width: max-content;
}
.project:not(.drag-flag, .active):hover,
.task:not(.drag-flag, .completed-task, .active):hover {
  background-color: #f7f7f7;
  border-color: var(--primary-color);
  border-width: 1px;
}
.project.active,
.task.active {
  border-color: var(--primary-color);
  border-width: var(--line-thickness-thin);
}
.completed-task span {
  color: var(--secondary-color);
  text-decoration: line-through;
}
.project-name-input {
  width: 100%;
  font-size: 16px;
  text-overflow: ellipsis;
  background-color: #ffffff00;
  border-style: solid;
  border-width: 1px;
  border-color: var(--outline-color);
  outline-color: var(--outline-color);
}
.project-name-input:read-only {
  outline: none;
  border-style: none;
  cursor: pointer;
}
.task-checkbox-wrapper {
  background-color: #a01b1b;
}

/* Task Custom Checkbox */
.task-status-container {
  position: relative;
  cursor: pointer;
  font-size: 22px;
  width: 25px;
  height: 25px;
  margin-right: 10px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.task-status-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.task-status-container .custom-checkbox {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border-radius: 50%;
  border-style: solid;
  border-width: 2px;
  border-color: #d8d8d8;
  background-color: #eee;
}
.task-status-container:hover input ~ .custom-checkbox {
  background-color: #ccc;
}
.task-status-container input:checked ~ .custom-checkbox {
  background-color: var(--outline-color);
}
.custom-checkbox:after {
  content: '';
  position: absolute;
  display: none;
}
.task-status-container input:checked ~ .custom-checkbox:after {
  display: block;
}
.task-status-container .custom-checkbox:after {
  left: 7px;
  top: 3px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

/* Task View Styles */
#task-view {
  flex: 1;
  padding: 20px;
  background-color: #f0f0f0;
}
#task-view h2 {
  margin-top: 0;
}
#tasks-container {
  display: grid;
  grid-template-columns: minmax(150px, 300px) 1fr;
  grid-template-rows: min-content min-content 1fr;
  height: 100%;
  gap: 10px;
}
#new-task-input {
  align-self: center;
  padding: 5px;
  border: 1px solid #ddd;
  border-radius: 3px;
  outline-color: var(--outline-color);
}
#add-task-btn {
  width: max-content;
}
#task-list {
  grid-column: 1 / -1;
  height: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  background: repeating-linear-gradient(
    #e4e4e4,
    #e4e4e4 var(--line-thickness-thin),
    #ffffff00 var(--line-thickness-thin),
    #ffffff00 calc(50px + var(--line-thickness-thin) + var(--list-margin))
  );
  background-position: 0 1px; /* Offset the background from the top */
}
.task {
  margin: var(--list-margin);
  padding: var(--list-padding);
  height: calc(50px + var(--line-thickness-thin));
}
.priority-icon svg {
  fill: var(--secondary-color);
  pointer-events: none;
}
.remove-task-btn svg,
.remove-project-btn svg,
.rename-project-btn svg {
  pointer-events: none;
}
.task > span {
  margin-right: auto;
  overflow: hidden;
  text-overflow: ellipsis;
}
.remove-item-dialog {
  display: grid;
  grid-template-columns: max-content max-content;
  grid-template-rows: auto;
  border: 1px solid #ccc;
  border-radius: 5px;
}
.remove-item-dialog h2 {
  grid-column: 1 / -1;
  max-width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0;
}
.remove-item-dialog p {
  margin: 10px 0;
  grid-column: 1 / -1;
}
.remove-item-dialog button {
  margin: 5px;
  grid-row-start: 3;
  background-color: var(--secondary-color);
}
.remove-item-dialog button:hover {
  background-color: var(--secondary-color-hover);
}

/* Task Panel Styles */
#task-view-panel {
  display: flex;
  flex-direction: column;
  padding: 10px 20px 20px 20px;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  width: var(--sidebar-width);
}

#close-task-panel-btn {
  align-self: flex-end;
  background-color: #d1d1d1;
  padding: 0;
  width: 24px;
  height: 24px;
}

#close-task-panel-btn:hover {
  background-color: #c0c0c0;
}

#close-task-panel-btn svg {
  fill: black;
  width: 100%;
  height: 100%;
}

#task-title {
  font-size: 1.2em;
  font-weight: bold;
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 10px;
  border: none;
  background: none;
  outline: none;
  text-overflow: ellipsis;
  border-bottom: 1px solid #dddddd;
}

#task-title:hover {
  background-color: #f1f1f1;
}

#task-title:focus {
  border-style: solid;
  border-width: 1px;
  border-color: var(--outline-color);
}

#due-date-container,
#priority-container {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

#due-date-container span,
#priority-container span {
  margin-right: 10px;
  min-width: 80px;
}

#task-due-date,
#task-priority {
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 3px;
  outline: none;
  font-size: 14px;
  flex: 1;
}

#notes-container {
  border: 1px solid #ddd;
  border-radius: 3px;
  padding: 10px;
  margin-top: 10px;
  background-color: #ffffff;
  cursor: text;
}

#task-notes {
  border: none;
  outline: none;
  font-size: 14px;
  line-height: 1.5;
  padding: 5px;
  width: 100%;
  resize: none; /* Disable manual resizing */
  overflow: hidden; /* Hide scrollbar */
}

#task-view-panel .remove-button {
  margin-top: auto;
  background-color: var(--secondary-color);
}
#task-view-panel .remove-button:hover {
  background-color: var(--secondary-color-hover);
}

/* Button Styles */
button {
  padding: 5px 10px;
  border: none;
  background-color: var(--primary-color);
  color: white;
  font-weight: bold;
  cursor: pointer;
  border-radius: 3px;
}
button:hover {
  background-color: var(--primary-color-hover);
}
button.remove-task-btn,
button.remove-project-btn {
  margin-left: 5px;
  background-color: var(--secondary-color);
}

button.remove-task-btn:hover,
button.remove-project-btn:hover {
  background-color: var(--secondary-color-hover);
}

button.rename-project-btn {
  margin-left: 5px;
  background-color: var(--outline-color);
}

button.rename-project-btn:hover {
  background-color: var(--outline-color-hover);
}

/* Footer styles */
footer {
  display: flex;
  font-size: small;
  justify-content: center;
  background-color: rgb(218, 218, 218);
  padding: 15px 0;
}
